import axios from 'axios';
import store from '@/store';

// 创建一个 axios 对象
// 创建的这个 axios 对象与 axios 基本一致
// 创建出来的 axios 对象可以做一些统一设置
const requestUtils = axios.create({
  baseURL: 'http://ttapi.research.itcast.cn/app/'
});

const request_token = axios.create({
  baseURL: 'http://ttapi.research.itcast.cn/app/'
});

// requestUtils.defaults.transformResponse = [function (data) {
//   // 对 data 进行任意转换处理
//   console.log(data);
// }]
// 1149222684006023168

// 设置请求拦截器
requestUtils.interceptors.request.use(
  config => {
    // 用于获取用户的 token
    const user = store.state.user;
    // 判断用户是否登陆，并且 token 是否存在
    if (user && user.token) {
      config.headers.Authorization = `Bearer ${user.token}`;
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

// 设置相应拦截器
requestUtils.interceptors.response.use(
  response => {
    return response.data.data || response.data;
  },
  async error => {
    let refresh_token = store.state.user.refresh_token;
    if (error.response.status === 401) {
      let res = await request_token({
        method: 'PUT',
        url: '/v1_0/authorizations',
        headers: {
          Authorization: `Bearer ${refresh_token}`
        }
      });
      store.commit('setUser', {
        token: res.data.data.token,
        refresh_token
      });
      return requestUtils(error.config)
    }
    return Promise.reject(error);
  }
);

export default requestUtils;